import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import Carousel, { CarouselItem } from "./Carousel"
import { GatsbyImage } from "gatsby-plugin-image"

const HomeCarousel = () => {
  const data = useStaticQuery(graphql`{
    allStrapiHomes {
      nodes {
        Images {
          localFile {
            childImageSharp {
              gatsbyImageData(layout: CONSTRAINED, formats: [AUTO, WEBP, AVIF])
            }
            name
          }
        }
      }
    }
  }`);

  return (<Carousel>
    {data.allStrapiHomes.nodes.map(nodes => (nodes.Images.map(result => (
      <CarouselItem>
        <GatsbyImage image={result.localFile.childImageSharp.gatsbyImageData} alt={result.localFile.name} className="home-images" />
      </CarouselItem>
    ))))}
  </Carousel>)
};

export default HomeCarousel;