import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import { GatsbyImage } from "gatsby-plugin-image"
import VideoLinkParser from "./videoLinkParser"

const RecentEvents = () => {
  try {
    const query = useStaticQuery(graphql`{
      allStrapiRecentEvents(filter: {Show: {eq: true}}) {
        nodes {
          Title
          Description
          VideoLink
          Image {
            localFile {
              childImageSharp {
                gatsbyImageData(layout: CONSTRAINED, formats: [AUTO, WEBP, AVIF])
              }
            }
          }
        }
      }
    }`);
    return (
      <React.Fragment>
        {
          query.allStrapiRecentEvents.nodes === [] ? <section className="home-section-title">
            <strong className="home-section-title-text">Recent Events</strong>
          </section> : null
        }
        {
          query.allStrapiRecentEvents.nodes.map((data, index) => {
            return (
              <section className={`section${index % 2 === 0 ? " home-colored-section" : ""}`}>
                <div className="home-widthLimit">
                  <div className="columns">
                    {
                      index % 2 === 0 ?
                        <div className="column is-three-fifths">
                          {
                            data.Image ? <GatsbyImage image={data.Image.localFile.childImageSharp.gatsbyImageData} alt={data.Title} style={{maxWidth: 542}} /> : null
                          }
                          {
                            data.VideoLink ? <VideoLinkParser links={data.VideoLink} title={data.Title} /> : null
                          }
                        </div>
                        :
                        <div className="column">
                          <strong className="home-event-title">
                            {data.Title}
                          </strong>
                          <span className="home-event-description">
                          {data.Description}
                        </span>
                        </div>
                    }
                    {
                      index % 2 === 0 ?
                        <div className="column">
                          <strong className="home-event-title">
                            {data.Title}
                          </strong>
                          <span className="home-event-description">
                          {data.Description}
                        </span>
                        </div>
                        :
                        <div className="column is-three-fifths">
                          {
                            data.Image ? <GatsbyImage image={data.Image.localFile.childImageSharp.gatsbyImageData} alt={data.Title} style={{maxWidth: 542}} className="home-images-right" /> : null
                          }
                          {
                            data.VideoLink ? <VideoLinkParser links={data.VideoLink} title={data.Title} /> : null
                          }
                        </div>
                    }
                  </div>
                </div>
              </section>
            )
          })
        }
      </React.Fragment>
    )
  } catch (e) {
    return null
  }
};

export default RecentEvents;